___CSS_LOADER_EXPORT___.push([module.id, `nav {
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 20px;
    background-color: rgb(251,249,247);
    width: clamp(280px, 20vw, 20vw);
    position: fixed;
    padding-right: 0;
}

/* PROFILE  */

nav .profile {
    display: flex;
    gap: 1em;
    align-items: center;
    padding-bottom: 1em;
    /* border-bottom: 3px solid gray; */
}

nav .profile img {
    border: 2px solid #ddd;
    border-radius: 50%;
}

/* TASKS  */

nav .new-task {
    display: flex;
    align-items: center;
    gap: 1em;
    width: 100%;
    margin-bottom: 1em;
    padding: 0.8em 0.5em;
}

nav .new-task img {
    border-radius: 50%;
    background-color: rgb(20, 250, 20);
}

nav .sort-options {
    display: flex;
    flex-direction: column;
    gap: 0.2em;
    margin-left: 0.5em;
    padding-bottom: 1.5em;
}

nav .sort-options img {
    max-width: 1.5em;
}

nav .sort-options button {
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 0.8em;
}

/* PROJECTS  */

nav .new-project {
    display: flex;
    width: 100%;
    align-items: center;
    gap: 1em;
    margin-bottom: 1em;
    padding: 0.8em 0.5em;
}

nav .new-project img {
    border-radius: 50%;
    background-color: rgb(255, 150, 150);
}

nav .projects-list button {
    display: flex;
    gap: 1em;
    align-items: center;
    width: 100%;
    padding: 0.8em;
}

nav .projects-list {
    display: flex;
    flex-direction: column;
    gap: 0.2em;
}

nav .projects-list img {
    max-width: 1.5em;
}

/* HOVERS  */

.projects-list button:hover, .sort-options button:hover {
    background-color: #eee;
    border-radius: 10px 0px 0px 10px;
    border-right: 5px solid cornflowerblue;
    font-weight: bold;
    color: cornflowerblue;
}

.new-task:hover {
    background-color: rgb(20, 250, 20, 10%);
    border-radius: 10px 0px 0px 10px;
    border-right: 5px solid green;
    font-weight: bold;
    color: green;
}

.new-project:hover {
    background-color: rgb(250, 20, 20, 5%);
    border-radius: 10px 0px 0px 10px;
    border-right: 5px solid red;
    font-weight: bold;
    color: red;
}

/* DELETE ICONS */

.delete-project {
    margin-left: auto;
}

.project-item .invisible {
    opacity: 0;
}`, "",{"version":3,"sources":["webpack://./src/styles/nav.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,aAAa;IACb,kCAAkC;IAClC,+BAA+B;IAC/B,eAAe;IACf,gBAAgB;AACpB;;AAEA,aAAa;;AAEb;IACI,aAAa;IACb,QAAQ;IACR,mBAAmB;IACnB,mBAAmB;IACnB,mCAAmC;AACvC;;AAEA;IACI,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA,WAAW;;AAEX;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,WAAW;IACX,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;IAClB,kCAAkC;AACtC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;IACV,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,cAAc;AAClB;;AAEA,cAAc;;AAEd;IACI,aAAa;IACb,WAAW;IACX,mBAAmB;IACnB,QAAQ;IACR,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;IAClB,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,mBAAmB;IACnB,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,gBAAgB;AACpB;;AAEA,YAAY;;AAEZ;IACI,sBAAsB;IACtB,gCAAgC;IAChC,sCAAsC;IACtC,iBAAiB;IACjB,qBAAqB;AACzB;;AAEA;IACI,uCAAuC;IACvC,gCAAgC;IAChC,6BAA6B;IAC7B,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,sCAAsC;IACtC,gCAAgC;IAChC,2BAA2B;IAC3B,iBAAiB;IACjB,UAAU;AACd;;AAEA,iBAAiB;;AAEjB;IACI,iBAAiB;AACrB;;AAEA;IACI,UAAU;AACd","sourcesContent":["nav {\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    gap: 1em;\n    padding: 20px;\n    background-color: rgb(251,249,247);\n    width: clamp(280px, 20vw, 20vw);\n    position: fixed;\n    padding-right: 0;\n}\n\n/* PROFILE  */\n\nnav .profile {\n    display: flex;\n    gap: 1em;\n    align-items: center;\n    padding-bottom: 1em;\n    /* border-bottom: 3px solid gray; */\n}\n\nnav .profile img {\n    border: 2px solid #ddd;\n    border-radius: 50%;\n}\n\n/* TASKS  */\n\nnav .new-task {\n    display: flex;\n    align-items: center;\n    gap: 1em;\n    width: 100%;\n    margin-bottom: 1em;\n    padding: 0.8em 0.5em;\n}\n\nnav .new-task img {\n    border-radius: 50%;\n    background-color: rgb(20, 250, 20);\n}\n\nnav .sort-options {\n    display: flex;\n    flex-direction: column;\n    gap: 0.2e